/* eslint-disable @typescript-eslint/no-unused-vars */
"use client";

import Link from "next/link";
import { useState, useEffect } from "react";
import { FaBell, FaEnvelope } from "react-icons/fa";
import { Badge, IconButton, Tooltip, Menu, MenuItem } from "@mui/material";
import { Notifications as NotificationsIcon, AccountCircle, Mail as MailIcon } from "@mui/icons-material";
import ThemeToggle from "./ThemeToggle"; // Import the ThemeToggle component

const Navbar = () => {
  const [notifCount, setNotifCount] = useState(0);
  const [msgCount, setMsgCount] = useState(0);
  const [anchorElNotif, setAnchorElNotif] = useState(null);
  const [anchorElProfile, setAnchorElProfile] = useState(null);
  const [anchorElMessages, setAnchorElMessages] = useState(null);

  useEffect(() => {
    setNotifCount(3); // Simulate notification count
    setMsgCount(2); // Simulate message count
  }, []);

  return (
    <div className="flex justify-between items-center p-4 bg-white dark:bg-gray-900 shadow-md">
      <h1 className="text-lg font-bold text-gray-800 dark:text-white">ðŸ“Œ EST Safi</h1>

      <div className="flex items-center gap-5">
        {/* Theme Toggle */}
        <ThemeToggle />

        {/* Notifications */}
        <Tooltip title="Notifications">
          <IconButton color="inherit" onClick={(e) => setAnchorElNotif(e.currentTarget)}>
            <Badge badgeContent={notifCount} color="error">
              <NotificationsIcon className="text-gray-600 dark:text-white" />
            </Badge>
          </IconButton>
        </Tooltip>

        {/* Messages */}
        <Tooltip title="Messages">
          <IconButton color="inherit" onClick={(e) => setAnchorElMessages(e.currentTarget)}>
            <Badge badgeContent={msgCount} color="primary">
              <MailIcon className="text-gray-600 dark:text-white" />
            </Badge>
          </IconButton>
        </Tooltip>

        {/* Profile */}
        <Tooltip title="Compte">
          <IconButton color="inherit" onClick={(e) => setAnchorElProfile(e.currentTarget)}>
            <AccountCircle className="text-gray-600 dark:text-white" />
          </IconButton>
        </Tooltip>
      </div>
    </div>
  );
};

export default Navbar;
